<template>
    <div class="text-center">
      <v-btn color="primary" @click="dialog = true">Abrir Demo</v-btn>
      <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dense color="primary" dark>
            <v-btn icon dark @click="dialog = false">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <v-toolbar-title>NO CONFORMIDADES - TABLEROS</v-toolbar-title>
          </v-toolbar>
  
          <v-card-text>
            <v-container fluid>
              <v-row>
                <!-- Panel de Filtros -->
                <v-col cols="3">
                  <v-card class="pa-4" color="light-blue lighten-4" style="position: fixed">
                    <!-- Filtros -->
                    <v-text-field v-model="filtroReference" label="Referencia" dense></v-text-field>
                    <v-text-field v-model="filtroNomenclature" label="Nomenclatura" dense></v-text-field>
                    <v-text-field v-model="filtroLocation" label="Ubicación" dense></v-text-field>
                    <v-select v-model="filtroTypeBoard" label="Tipo de Tablero" dense :items="tiposDeTablero" @change="onFilterChange"></v-select>
                    <v-text-field v-model="filtroAbility" label="Capacidad" dense></v-text-field>
                    <v-select v-model="filtroBrand" label="Marca" dense :items="marcasUnicas" @change="onFilterChange"></v-select>
                    <v-select v-model="filtroNom" label="Nom" dense :items="nomsUnicos" @change="onFilterChange"></v-select>
                    <v-btn color="primary" @click="limpiarFiltros">Limpiar Filtros</v-btn>
                    
                    <!-- Cantidad de datos y estadísticas -->
                    <div class="mt-4">
                      <div>NO CONFORMIDADES: {{ itemsFiltradosCopy.length }}</div>
                      <!-- Agrega aquí las estadísticas que desees mostrar -->
                    </div>
                  </v-card>
                </v-col>
  
                <!-- Lista de Resultados -->
                <v-col cols="9">
                  <v-row v-if="itemsFiltradosCopy.length > 0">
                    <v-col v-for="(item, index) in itemsFiltradosCopy" :key="index" cols="4">
                      <v-slide-y-transition>
                        <v-card class="mb-4" height="100%">
                          <v-card-text>
                            <div><b>Referencia:</b> {{ item.reference }}</div>
                            <div><b>Nomenclatura:</b> {{ item.nomenclature }}</div>
                            <div><b>Ubicación:</b> {{ item.location }}</div>
                            <div><b>Tipo de Tablero:</b> {{ item.typeBoard }}</div>
                            <div><b>Capacidad:</b> {{ item.ability }}</div>
                            <div><b>Marca:</b> {{ item.brand }}</div>
                            <div><b>Nom:</b> {{ item.nom }}</div>
                            <div><b>Descripción:</b> {{ item.description }}</div>
                          </v-card-text>
                        </v-card>
                      </v-slide-y-transition>
                    </v-col>
                  </v-row>
                  <!-- Si no hay resultados, mostrar un mensaje -->
                  <v-row v-else>
                    <v-col cols="12" class="text-center">
                      <p>No se encontraron resultados.</p>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-card>
      </v-dialog>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      items: {
        type: Array,
        required: true
      }
    },
    data() {
      return {
        dialog: false,
        filtroReference: '',
        filtroNomenclature: '',
        filtroLocation: '',
        filtroTypeBoard: '',
        filtroAbility: '',
        filtroBrand: '',
        filtroNom: '',
        itemsFiltrados: [],
        itemsFiltradosCopy: [],
        tiposDeTablero: [], // Array vacío para almacenar los tipos de tablero únicos
        marcasUnicas: [], // Define las opciones para el selector de Marca
        nomsUnicos: [] // Define las opciones para el selector de Nom
      };
    },
    created() {
      this.limpiarDatos();
    },
    watch: {
      items() {
        this.limpiarDatos();
      },
      filtroReference() {
        this.filtrarItems();
      },
      filtroNomenclature() {
        this.filtrarItems();
      },
      filtroLocation() {
        this.filtrarItems();
      },
      filtroTypeBoard() {
        this.filtrarItems();
      },
      filtroAbility() {
        this.filtrarItems();
      },
      filtroBrand() {
        this.filtrarItems();
      },
      filtroNom() {
        this.filtrarItems();
      }
    },
    methods: {
      limpiarDatos() {
        // Mapear los datos de result y crear una nueva lista con el formato especificado
        let newData = [];
        this.items.forEach(item => {
          item.result.forEach(resultItem => {
            if (resultItem.value === 'no') {
              newData.push({
                reference: item.reference,
                nomenclature: item.nomenclature,
                location: item.location,
                typeBoard: item.typeBoard,
                ability: item.ability,
                brand: item.brand,
                nom: resultItem.nom,
                description: resultItem.description
              });
            }
          });
        });
        // Asignar la nueva lista a itemsFiltrados
        this.itemsFiltrados = newData;
        this.itemsFiltradosCopy = newData;
        
        // Obtener tipos de tablero únicos
        this.tiposDeTablero = [...new Set(newData.map(item => item.typeBoard))];
        // Obtener marcas únicas
        this.marcasUnicas = [...new Set(newData.map(item => item.brand))];
        // Obtener noms únicos
        this.nomsUnicos = [...new Set(newData.map(item => item.nom))];
      },
      filtrarItems() {
        console.log('filtros');
        this.itemsFiltradosCopy = JSON.parse(JSON.stringify(this.itemsFiltrados));
        this.itemsFiltradosCopy = this.itemsFiltradosCopy.filter(item => {
          const referenceMatch = item.reference.toString().toLowerCase().includes(this.filtroReference.toLowerCase());
          const nomenclatureMatch = item.nomenclature.toLowerCase().includes(this.filtroNomenclature.toLowerCase());
          const locationMatch = item.location.toLowerCase().includes(this.filtroLocation.toLowerCase());
          const typeBoardMatch = item.typeBoard.toLowerCase().includes(this.filtroTypeBoard.toLowerCase());
          const abilityMatch = item.ability.toString().toLowerCase().includes(this.filtroAbility.toLowerCase());
          const brandMatch = this.filtroBrand === '' || item.brand === this.filtroBrand;
          const nomMatch = this.filtroNom === '' || item.nom === this.filtroNom;
          return referenceMatch && nomenclatureMatch && locationMatch && typeBoardMatch && abilityMatch && brandMatch && nomMatch;
        });
      },
      limpiarFiltros() {
        this.filtroReference = '';
        this.filtroNomenclature = '';
        this.filtroLocation = '';
        this.filtroTypeBoard = '';
        this.filtroAbility = '';
        this.filtroBrand = '';
        this.filtroNom = '';
        this.filtrarItems();
      },
      onFilterChange() {
        this.filtrarItems();
      }
    }
  };
  </script>
  