<template>
  <v-card elevation="0" class="mx-auto" color="#F2F4F4" height="100vh">
    <BoardRead :items="items"  style="padding: 20px;"/>
    <div v-if="items.length === 0">
      <p class="text-center">..Sin Tableros...</p>
    </div>
    <div>
      <v-virtual-scroll :items="items" :item-height="80" style="height: 91vh">
        <template v-slot:default="{ item }">
          <v-card elevation="0" outlined>
            <div>
              <v-list-item @click="open(item)">
                <v-list-item-action>
                  <v-btn fab small depressed :color="isHotSpots(item)" class="white--text">
                    {{ item.reference }}
                  </v-btn>
                </v-list-item-action>

                <v-list-item-content>
                  <v-list-item-title>
                    <strong> {{ item.nomenclature }}</strong>
                  </v-list-item-title>
                  <v-list-item-subtitle>{{
                    item.location
                  }}</v-list-item-subtitle>
                </v-list-item-content>

                <BtnActionSection :item="item" nameEdit="boardList" @newItem="$refs.board.create();" />
              </v-list-item>
            </div>
          </v-card>
        </template>
      </v-virtual-scroll>
    </div>

    <board-new ref="board" />
  </v-card>
</template>
<script>
import { mapState } from "vuex";
import BoardNew from "./BoardNew.vue";
import BoardRead from "./BoardRead.vue"
export default {
  components: {
    BoardNew, BoardRead, BtnActionSection: () => import("@/components/global/BtnActionSection.vue")
  },
  name: "BoardList",
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    items() {
      return this.data.boardList.data;
    },
    config() {
      return this.data.boardList.config;
    },
  },
  //Aqui se hacen las operaciones de crear, actualizar y eliminar -- pero hasta el final..
  methods: {
    open(item) {
      this.$refs.board.openBoard(item);
      /*  item.result.forEach((element) => {
         console.log(element.value);
       }); */
    },
    isHotSpots(item) {
      if (!item.result) return '#273746'
      const find = item.result.find(e => e.nom == '110-14.C');

      if (find.value && find.value == "no" && find.itemsT.length > 0) return '#E74C3C'

      return '#273746'
    }
  },
};
</script>